import { Component, OnInit } from '@angular/core';
import {FormControl} from '@angular/forms';
import {Observable} from 'rxjs';
import {map, startWith} from 'rxjs/operators';
import {MatDialog} from '@angular/material/dialog';

@Component({
  selector: 'app-header-class-room',
  templateUrl: './header-class-room.component.html',
  styleUrls: ['./header-class-room.component.scss']
})
export class HeaderClassRoomComponent implements OnInit {
  myControl = new FormControl();
  options: string[] = ['One', 'Two', 'Three'];
  filteredOptions: Observable<string[]>;
  foods= [
    {value: 'steak-0', viewValue: 'Steak'},
    {value: 'pizza-1', viewValue: 'Pizza'},
    {value: 'tacos-2', viewValue: 'Tacos'}
  ];

  constructor(public dialog: MatDialog) { }

  ngOnInit(): void {
    this.filteredOptions = this.myControl.valueChanges.pipe(
      startWith(''),
      map(value => this._filter(value))
    );
  }
  private _filter(value: string): string[] {
    const filterValue = value.toLowerCase();

    return this.options.filter(option => option.toLowerCase().indexOf(filterValue) === 0);
  }
  scroll(){
   scroll(0,600)
  }
  openDialog() {
    this.dialog.open(ConstruyendoCardComponent);
  }
}
@Component({
  selector: 'app-construyendo-card',
  templateUrl: '../construyendo-card/construyendo-card.component.html',
  styleUrls: ['./header-class-room.component.scss']
})
  export class ConstruyendoCardComponent  {}