import { Component, OnInit, DoCheck } from '@angular/core';

@Component({
  selector: 'app-carrusel',
  templateUrl: './carrusel.component.html',
  styleUrls: ['./carrusel.component.scss']
})
export class CarruselComponent implements OnInit,DoCheck  {
  images = [
    "../../../assets/pexels-fauxels-3184328.jpg",
    "../../../assets/pexels-kaboompics-com-6127.jpg",
    "../../../assets/ship-3493887_1920.jpg",
    "../../../assets/container-port-284429_1920.jpg",
    "../../../assets/Higiene-y-Seguridad-Industrial.jpg",
    "../../../assets/turtle-863336_1280.jpg"
  ];
  titles=[
    "Aula Virtual",
    "Curso Basico OMI 1.24",
    "Curso OMI 9.25",
    "Curso Avanzado OMI 7.12",
    "Curso Basico OMI 2.58",
    "Curso Basico OMI 3.58"
  ];
  messages=[
    "Formacion para gente de mar",
    "Primeros Auxilios",
    "Navegacion en Zona Portuaria",
    "Carga y Descarga de Mercancia",
    "Seguridad Laboral",
    "Proteccion del Ambiente Marino"
  ];
  title: String="";
  message:string ="";
  image: string = "";
  cont=0;
  conta=0;
  status= false;
 
  constructor() { 
  }

  ngOnInit(): void {
    this.image= this.images[0];
    this.message= this.messages[0];
    this.title = this.titles[0];
    setTimeout(() => {
      this.change();
    }, 10000);
  }
  ngDoCheck(){
    //this.change();
  }
  change(){
    for(var i=0; i < this.images.length; i++){
      console.log(this.images[i])
       setTimeout(() => {
        console.log(i);
      }, 5000);
    }
  }
  changeImage(){
    this.cont = this.cont +1;
    if(this.images.length -1 == this.cont){
      this.cont = 0;
    }
    console.log(this.images[this.cont]);
    this.image = this.images[this.cont];  
  }
  changeRigth(){
    this.cont = this.cont +1;
    this.image = this.images[this.cont];
    this.message = this.messages[this.cont];
    this.title = this.titles[this.cont];
     
  }
  changeLeft(){
    this.cont = this.cont -1;
    this.image = this.images[this.cont]; 
    this.message = this.messages[this.cont]; 
    this.title = this.titles[this.cont];
  } 

}
